ul {
  padding-left: 0;
}

a:hover {
  text-decoration: none;
}

.logo {
  -webkit-animation-name: rotate; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 4s; /* Safari 4.0 - 8.0 */
  animation-name: rotate;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

.svg-pan--anim {
  transform-origin: center bottom;
  -webkit-animation-name: pan;
  -webkit-animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
  animation-name: pan;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

.img {
  width: 100%;
  height: auto;
}

.nav {
  &bar {
    background-color: $white;

    &-dark {
      & .nav-link {
        color: $white !important;
      }
    }
  }

  &-link {
    color: $dark !important;
    font-family: $font-Work, sans-serif;
    font-size: 14px;
    font-weight: 600;
  }

  &-item {
    padding: 3px 10px;
    text-transform: uppercase;
    margin-left: 20px;

    &--border {
      border: 1.6px solid #292F42;
      border-radius: 5px;
    }
  }
}

aside {
  & > ul {
    margin-top: 100px;
    list-style: none;

    & > li {
      padding: 8px 0;

      a {
        font-family: $font-Work, sans-serif;
        font-weight: normal;
        font-size: 16px;
        text-transform: capitalize;
        color: $grey;
      }
    }
  }
}

.aside {
  &_item {
    &-active {
      color: $dark;
    }
  }
}

.link {
  margin: 40px 0;

  &-back {
    font-family: $font-Work, sans-serif;
    color: $dark !important;
    font-size: 24px;
  }
}

.position {
  font-family: $font-Work, sans-serif;
  font-size: 16px;
  color: $grey;
  margin-left: 10px;

  &-img {
    padding-left: 15px;
  }

  &-collection {
    margin-top: 15px;

    & span {
      color: $green;
      text-decoration: underline;
    }
  }

  &-op {
    opacity: 0.25;
  }
}

.personals {
  &-list {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  &-item {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;

    &--text {
      font-family: $font-Work, sans-serif;

      & > h5 {
        padding-top: 10px;
        font-size: 24px;
        font-weight: 600;
        color: $dark;
      }

      & > p {
        font-size: 16px;
        color: $grey;
      }
    }

    &:hover {
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
    }

    &:hover .personals-link {
      display: block;
      color: $light-green;
    }
  }

  &-link {
    display: none;

    & a {
      color: $green
    }
  }
}

.see {
  &-wrapper {
    margin-top: 100px;
  }

  &-text {
    & h3 {
      font-family: $font-lora, Serif;
      font-size: 34px;
      color: $light-green;
      font-weight: bold;
    }

    & p {
      text-transform: uppercase;
      font-family: $font-Work, sans-serif;
      color: $grey;
      font-size: 14px;
      opacity: 0.25;
    }
  }

  &-btn {
    background: linear-gradient(251.94deg, #C4E58B -41.78%, #86BE26 63.15%);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
    color: $white;
    font-family: $font-Work, sans-serif;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    -webkit-animation-name: grad; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 5s; /* Safari 4.0 - 8.0 */
    animation-name: grad;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
  }
}


.footer {
  background-color: #292F42;

  &-wrapper {
  }

  &-box {
    color: #fff;
    font-family: $font-lora, Serif;
    font-size: 24px;

    & h3 {
      font-weight: bold;
    }

    & p {
      margin-top: 15px;
      margin-bottom: 15px;
      font-family: $font-Work, sans-serif;
      font-size: 16px;
    }
  }

  &-button {
    &--green {
      background: linear-gradient(270deg, #86BE26 1.79%, #86BE26 100%);;
    }
  }

  &-list {
    font-family: $font-lora, Serif;
    font-size: 20px;
    font-weight: bold;
    list-style: none;

    &s > div {
      width: 33%;
    }

    &--item {
      & a {
        font-family: "Roboto", sans-serif;
        font-size: 14px;
        color: white;
        font-weight: normal;

        &:hover {
          color: $light-green
        }
      }
    }
  }

  &--toggle-item {
    display: none;
  }
}

.cp {
  & hr {
    margin: 0;
    border: 1px dotted #000000;
    border-style: none none dotted;
    color: #fff;
    background-color: #292f42;
  }

  text-transform: uppercase;
  font-family: $font-Work;
  font-size: 12px;
  color: white;
  font-weight: 600;
  background-color: #292f42;

  & span {
    color: $light-green;
  }

  &-box {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

@media only screen and (max-device-width: 1000px) {
  .nav {
    &-item {
      text-align: center;
    }
    &-link {

      & svg {
        display: none;
      }
    }

    &-item--border {
      text-align: center;
      background: linear-gradient(247.78deg, #C4E58B -41.78%, #86BE26 63.15%);
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
    }
  }
}

@media only screen and (max-device-width: 720px) and (min-device-width: 576px) {
  .footer {
    &-box {
      &--item {
        width: 48%;

        & button {
          height: 38px;
        }
      }
    }

    &-group {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
    }
  }
}

@media only screen and (max-device-width: 576px) {
  .footer {
    &-box {
      &--item {
        width: 100%;

        & button {
          height: 38px;
        }
      }
    }

    &-list {
      display: none;

      &s {
        flex-direction: column;
      }

    }

    &--toggle-item {
      display: block;
      font-size: 26px;
      font-weight: bold;
      font-family: $font-Work, sans-serif;
      position: absolute;
      top: 0;
      right: 0;
      color: white;
    }
  }
  .list--toggle {
    position: relative;
    width: 100% !important;
    padding-right: 30px;
  }
  .personals {
    &:hover .personals-link {
      display: none;
      color: $light-green;
    }
  }
  .navbar {
    &-dark {
      background-color: $dark;
    }
  }
  .nav {
    &-link {
      text-align: left;
      & svg {
        display: none;
      }
    }

    &-item--border {
      background: linear-gradient(247.78deg, #C4E58B -41.78%, #86BE26 63.15%);
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);

      & a {
        text-align: center;
      }
    }
  }
}