@-webkit-keyframes rotate {
  from {transform: rotate(0deg)}
  to {transform: rotate(360deg)}
}

/* Standard syntax */
@keyframes rotate {
  from {transform: rotate(0deg)}
  to {transform: rotate(360deg)}
}
@-webkit-keyframes grad {
  from {    background: linear-gradient(251.94deg, #C4E58B -41.78%, #86BE26 63.15%)}
  to {     background: linear-gradient(611.94deg, #C4E58B -41.78%, #86BE26 63.15%)}
}
@keyframes grad {
  from {    background: linear-gradient(251.94deg, #C4E58B -41.78%, #86BE26 63.15%)}
  to {     background: linear-gradient(611.94deg, #C4E58B -41.78%, #86BE26 63.15%)}

}

@-webkit-keyframes pan {
  0% { transform: rotate(0)}
  50% { transform: rotate(-15deg)}
  100% { transform: rotate(0)}
}

@keyframes pan {
  0% { transform: rotate(0)}
  50% { transform: rotate(-15deg)}
  100% { transform: rotate(0)}

}
